@import url(https://fonts.googleapis.com/css2?family=Teko:wght@600&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Teko', sans-serif;
}
h1{
    font-size: 45px;
    margin-bottom: 30px;
}
.tc{
    text-align: center;
}
.container{
    width: 1170px;
    margin:0 auto;
}
.card{
    background: #6739ba;
    display: inline-block;
    margin: 0px 10px 15px 0px;
    padding: 15px;
    width: 292px;
    height: auto;
    text-align: center;
    border-radius: 5px;
    transition: all 0.5s ease-in-out;
}
.card img{
    width: 100%;
    height: auto%;
    display: block;
}
.card h3{
    text-transform: capitalize;
    font-size: 25px;
    margin: 18px 0px 0px 0px;
}
.card p{
    color: #252525;
    font-size: 18px;
    margin-top: 0px;
}
.card:hover{
    transform: scale(1.03);
}

button{
    display: block;
    font-size: 15px;
    font-weight: 600;
    border: none;
    background: #a279ea;
    border-radius: 10px;
    padding: 12px 28px;
    margin: 30px auto 0px;
    outline: none;
}
button:hover{
    background: #6739ba;
    color: #fff;
}
.person-wrapper{
    align-items: center;
    display: flex;
    justify-content: center;
}
@media screen and (max-width: 1199px) {
    .container {
        width: 960px; 
    }
}
@media screen and (max-width:992px){
    .container {
        width:750px;
    }
}
@media screen and (max-width: 767px){
    .container{
        width: auto;
        padding: 0px 15px;
    }
    .person-wrapper{
        flex-wrap: wrap;
    }
}
